<template>
  <div class="leaflet">
    <div id="leafletMap"></div>
    <div id="leafletMap1"></div>
    <a-button type="primary" @click="remove" class="remove-btn">清除</a-button>
  </div>
</template>
<script setup>
import { onMounted, reactive, ref } from "vue";

console.log("leaflet", L);
onMounted(() => {
  initMap();
  initMap1();
});
let map = reactive();
const initMap = () => {
  map = L.map("leafletMap", {
    center: [39.90553, 116.391305],
    zoom: 17,
    maxZoom: 26,
  });
  L.tileLayer
    .chinaProvider("GaoDe.Satellite.Map", {
      maxZoom: 26,
      maxNativeZoom: 18,
    })
    .addTo(map);
  //添加纠偏参照物
  L.marker([22.88873892277479, 113.40005278587341])
    .addTo(map)
    .bindPopup("<p>我是WGS84坐标下，天安门广场国旗所在位置</p>")
    .openPopup();
};
let map1 = reactive();
const initMap1 = () => {
  map1 = L.map("leafletMap1", {
    center: [39.90553, 116.391305],
    zoom: 17,
    maxZoom: 26,
  });
  // L.tileLayer(
  //   "https://gac-geo.googlecnapps.cn/maps/vt?lyrs=s&x={x}&y={y}&z={z}",
  //   {
  //     minZoom: 3,
  //     maxZoom: 18,
  //   }
  // ).addTo(map1);
  L.tileLayer
    .chinaProvider("Google.Satellite.Annotion", {
      maxZoom: 26,
      maxNativeZoom: 18,
    })
    // https://gac-geo.googlecnapps.cn/maps/vt?lyrs=s&x={x}&y={y}&z={z}
    .addTo(map1);
  // //添加纠偏参照物
  L.marker([22.88873892277479, 113.40005278587341])
    .addTo(map1)
    .bindPopup("<p>我是WGS84坐标下，天安门广场国旗所在位置</p>")
    .openPopup();
};
const arr = ref([]);
const polyline = ref(null);
const polyline1 = ref(null);

const clickEvent = (e) => {
  console.log("点击事件", e);
  arr.value.push(e.latlng);
  console.log("数组", arr.value);
  if (arr.value && arr.value.length > 1) {
    if (!polyline.value) {
      polyline.value = L.polyline(arr.value, {
        color: "red",
      }).addTo(map);
      polyline1.value = L.polyline(arr.value, {
        color: "red",
      }).addTo(map1);
    } else {
      polyline.value.addLatLng(e.latlng);
      polyline1.value.addLatLng(e.latlng);
    }
  }
};
const remove = () => {
  map.removeLayer(polyline.value);
  map1.removeLayer(polyline1.value);
  arr.value = [];
  polyline.value = "";
  polyline1.value = "";
};
</script>
<style lang="scss" scoped>
.leaflet {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  #leafletMap {
    width: 49%;
    height: 100%;
  }
  #leafletMap1 {
    width: 49%;
    height: 100%;
  }
  .remove-btn {
    position: absolute;
    bottom: 100px;
    left: 49%;
    z-index: 1000;
  }
}
</style>